import React from 'react';
import { CopyBlock, dracula } from 'react-code-blocks';

const CSS_CODE = `/* Default style for all challenges containers. */
.challenge-container {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;

  /* Overridden for this component */
  width: 100%;
  justify-content: center;
}

.lighthing-text {
  font-size: 48px;
  font-weight: bold;
  text-transform: uppercase;
}

.letter {
  animation: lightining 2s linear infinite;
}

.group-01 .letter:nth-child(1) {
  animation-delay: 0;
}

.group-01 .letter:nth-child(2) {
  animation-delay: 0.1s;
}

.group-01 .letter:nth-child(3) {
  animation-delay: 0.2s;
}

.group-01 .letter:nth-child(4) {
  animation-delay: 0.3s;
}

.group-01 .letter:nth-child(5) {
  animation-delay: 0.4s;
}

.group-01 .letter:nth-child(6) {
  animation-delay: 0.5s;
}

.group-02 .letter:nth-child(1) {
  animation-delay: 0.6s;
}

.group-02 .letter:nth-child(2) {
  animation-delay: 0.7s;
}

.group-02 .letter:nth-child(3) {
  animation-delay: 0.8s;
}

.group-02 .letter:nth-child(4) {
  animation-delay: 0.9s;
}

.group-03 .letter:nth-child(1) {
  animation-delay: 1s;
}

.group-03 .letter:nth-child(2) {
  animation-delay: 1.1s;
}

.group-04 .letter:nth-child(1) {
  animation-delay: 1.2s;
}

.group-04 .letter:nth-child(2) {
  animation-delay: 1.3s;
}

.group-04 .letter:nth-child(3) {
  animation-delay: 1.4s;
}

@keyframes lightining {
  0% {
    color: #ffffff;
    text-shadow: none;
  }
  90% {
    color: #ffffff;
    text-shadow: none;
  }
  100% {
    color: #55acee;
    text-shadow: 0 0 7px #55acee, 0 0 50px #55acee83;
  }
}
`;

const Code: React.FC = () => {
  return (
    <div className="container-code">
      <CopyBlock
        text={CSS_CODE}
        language="css"
        showLineNumbers={false}
        theme={dracula}
        onCopy={false}
      />
    </div>
  );
};

export default Code;
